@import "../../styles/variables";

.card-container {
  display: flex;
  justify-content: center;
  margin: 0 1rem 3rem 1rem;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  color: $card-text-color;

  .card {
    width: 330px;
    height: 430px;
    background: white;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: center;
    border-radius: 0.2rem;

    h3 {
      margin-bottom: 0;
    }

    .card-image {
      img {
        width: 100%;
        height: 200px;
      }
    }

    .card-description {
      margin: 0 1rem;
    }

    svg {
      margin: 0 0.3rem;
    }

    .card-links {
      display: flex;
      justify-content: space-around;
      margin-bottom: 20px;
      a {
        text-decoration: none;
        color: black;

        &:hover {
          box-shadow: 0 0.2rem black;
        }
      }
    }
  }
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  .card-container {
    .card {
      width: 100%;
      max-width: 300px;
      height: 430px;

      .card-description {
        margin: 0 2rem;
      }
    }
  }
}
