@import url(https://fonts.googleapis.com/css?family=Reem+Kufi|Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i);
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #374850; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

#nav {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #374850;
  display: flex;
  flex-direction: row;
  padding-bottom: 1rem; }
  #nav .navbar-links {
    display: flex;
    justify-content: flex-end;
    width: 100%; }
  #nav .file-download {
    margin-left: 0.5rem; }
  #nav .hamburger {
    display: none; }
  #nav a {
    display: flex;
    font-size: 1.2rem;
    text-decoration: none;
    padding: 0 0.4rem;
    color: white;
    margin-right: 1rem;
    margin-top: 1rem;
    -webkit-transition: 0.5s;
    transition: 0.5s; }
    #nav a:hover {
      box-shadow: 0 0.2rem white; }
  #nav .lng {
    font-weight: 400;
    font-style: italic;
    border: 1px solid white; }

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  #nav {
    flex-direction: column; }
    #nav .navbar-links {
      display: none; }
      #nav .navbar-links.active {
        display: flex;
        flex-direction: column; }
        #nav .navbar-links.active a {
          align-self: center;
          font-size: 1.4rem; }
    #nav .hamburger {
      display: block;
      margin-top: 0.5rem;
      margin-right: 0.5rem;
      align-self: flex-end;
      text-align: start; } }

#header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  height: 100vh;
  color: white;
  font-size: 2rem; }
  #header h1 {
    font-family: "Reem Kufi", sans-serif;
    margin: 0;
    font-size: 5rem;
    font-weight: 600; }
  #header h2 {
    font-weight: 200;
    font-size: 2rem;
    margin-top: 0; }
  #header h3 {
    font-weight: 200;
    font-size: 1.3rem;
    text-align: center;
    max-width: 30rem;
    margin: 0 auto 1rem auto; }
  #header svg {
    margin: 0 1rem 8rem 1rem; }

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  #header h1 {
    font-size: 3rem; } }

#about {
  color: white;
  width: 60%;
  margin: 0 auto;
  margin-bottom: 10rem; }
  #about h2 {
    font-size: 3rem;
    padding-top: 3rem; }
  #about p {
    font-size: 1.2rem;
    font-weight: 200; }

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  #about {
    width: 90%; }
    #about h2 {
      text-align: center; } }

#portfolio {
  width: 60%;
  margin: 0 auto;
  display: flex;
  flex-direction: column; }
  #portfolio h1 {
    color: white;
    font-size: 3rem; }
  #portfolio .cards-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: space-between; }

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  #portfolio {
    width: 90%; }
    #portfolio h1 {
      text-align: center; } }

.card-container {
  display: flex;
  justify-content: center;
  margin: 0 1rem 3rem 1rem;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  color: #374850; }
  .card-container .card {
    width: 330px;
    height: 430px;
    background: white;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: center;
    border-radius: 0.2rem; }
    .card-container .card h3 {
      margin-bottom: 0; }
    .card-container .card .card-image img {
      width: 100%;
      height: 200px; }
    .card-container .card .card-description {
      margin: 0 1rem; }
    .card-container .card svg {
      margin: 0 0.3rem; }
    .card-container .card .card-links {
      display: flex;
      justify-content: space-around;
      margin-bottom: 20px; }
      .card-container .card .card-links a {
        text-decoration: none;
        color: black; }
        .card-container .card .card-links a:hover {
          box-shadow: 0 0.2rem black; }

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  .card-container .card {
    width: 100%;
    max-width: 300px;
    height: 430px; }
    .card-container .card .card-description {
      margin: 0 2rem; } }

#tech {
  width: 60%;
  margin: 0 auto; }
  #tech .skills-container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap; }
  #tech h1 {
    color: white;
    font-size: 3rem;
    padding-top: 3rem; }
  #tech .scroll-top {
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center; }
    #tech .scroll-top span {
      font-size: 0.6rem; }

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  #tech {
    width: 90%; }
    #tech h1 {
      text-align: center; } }

.skill-container {
  background: white;
  text-align: center;
  border-radius: 0.3rem;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  width: 23rem;
  padding: 3rem 0;
  margin-bottom: 3rem;
  color: #374850; }
  .skill-container .skill-description {
    margin: 0 1rem; }
  .skill-container .skills {
    display: flex;
    flex-direction: column;
    height: 13rem;
    justify-content: space-evenly; }
    .skill-container .skills span {
      margin-top: 0.5rem; }

